@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Poppins:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{background-color:#eae2b740}.poppin-font,.section-title,nav a,.home .experience .experience-wrapper .duration{font-family:'Poppins', 'Courier New', Courier, monospace;color:#533747;text-decoration:none;letter-spacing:2px}.section-title{text-align:left;width:100%;margin-top:100px}nav{max-width:1920px;height:100px;display:flex;align-items:center;margin:0 auto}nav a.active{color:#f3752b}nav .nav-item{padding-right:40px}.home{display:flex;align-items:center;flex-direction:column;transition:all 300s ease-in-out}.home section{max-width:1920px}.home .header{font-family:'PT Serif';height:50vh;display:flex;flex-direction:column;justify-content:center}.home .header .intro{font-size:36px;line-height:72px;font-weight:700;color:#989898}@media only screen and (max-width: 667px){.home .header .intro{font-size:30px}}.home .header .subtitle{font-size:62px;font-weight:600;color:#98557b}@media only screen and (max-width: 667px){.home .header .subtitle{font-size:40px;margin-top:5px}}.home .header .subtitle .subtitle-diff{font-family:'Caveat';font-size:76px;color:#f3752b}@media only screen and (max-width: 667px){.home .header .subtitle .subtitle-diff{font-size:50px}}.home .half-half{display:flex;max-width:1920px;justify-content:space-between}@media only screen and (max-width: 667px){.home .half-half{flex-direction:column}}.home .experience{min-height:250px;display:flex;justify-content:center;flex-direction:column;background-color:#ffffff4f;margin:20px 0;border-radius:50px;box-shadow:0px 12px 24px -14px #d2d2d2;transition:300ms ease-in all;cursor:pointer}@media only screen and (max-width: 667px){.home .experience{width:100%;border-radius:20px}}.home .experience:hover{box-shadow:0px 12px 24px -7px #d2d2d2}.home .experience .experience-wrapper{display:flex;justify-content:space-evenly}@media only screen and (max-width: 667px){.home .experience .experience-wrapper{flex-direction:column-reverse}}.home .experience .experience-wrapper .logo{width:30%;display:flex;flex-direction:column;justify-content:center}.home .experience .experience-wrapper .logo img{width:100%;position:relative;top:50%;transform:translateY(-50%);display:none}@media only screen and (max-width: 667px){.home .experience .experience-wrapper .logo{padding:5px 20px;width:calc(100% - 40px)}}.home .experience .experience-wrapper .experience-body{width:50%;color:#533747;display:flex;flex-direction:column;justify-content:center}@media only screen and (max-width: 667px){.home .experience .experience-wrapper .experience-body{width:calc(100% - 40px);margin:0 auto}}.home .experience .experience-wrapper .experience-body .company{font-family:'PT Serif';font-size:42px;font-weight:600}@media only screen and (max-width: 667px){.home .experience .experience-wrapper .experience-body .company{font-size:30px}}.home .experience .experience-wrapper .designation{font-family:'PT Serif';font-size:24px}@media only screen and (max-width: 667px){.home .experience .experience-wrapper .designation{font-size:16px}}.home .experience .experience-wrapper .duration{letter-spacing:unset;margin-top:7px}@media only screen and (max-width: 667px){.home .experience .experience-wrapper .duration{font-size:14px;margin-top:5px}}.footer .fa{padding:0 10px;font-size:22px}.footer .poppin-font p,.footer .section-title p,.footer nav a p,nav .footer a p,.footer .home .experience .experience-wrapper .duration p,.home .experience .experience-wrapper .footer .duration p{padding-top:10px}@media only screen and (max-width: 667px){.footer .poppin-font p,.footer .section-title p,.footer nav a p,nav .footer a p,.footer .home .experience .experience-wrapper .duration p,.home .experience .experience-wrapper .footer .duration p{font-size:10px}}

